body {
  font-family: "Montserrat", sans-serif;
  background: #112233; }

.weather-card {
  margin: 60px auto;
  height: 640px;
  width: 450px;
  background: #fff;
  box-shadow: 0 1px 38px rgba(0, 0, 0, 0.15), 0 5px 12px rgba(0, 0, 0, 0.25);
  overflow: hidden; }
  .weather-card .top {
    position: relative;
    height: 400px;
    width: 100%;
    overflow: hidden;
    background: url(/react-weather-app/static/media/clear.803ff5aa.jpg) no-repeat;
    background-size: cover;
    background-position: center center;
    text-align: center; }
    .weather-card .top .wrapper {
      padding: 30px;
      position: relative;
      z-index: 1; }
      .weather-card .top .wrapper .heading {
        margin-top: 20px;
        font-size: 35px;
        font-weight: 400;
        color: #fff; }
      .weather-card .top .wrapper .location {
        margin-top: 20px;
        font-size: 21px;
        font-weight: 400;
        color: #fff; }
      .weather-card .top .wrapper .temp {
        margin-top: 20px; }
        .weather-card .top .wrapper .temp a {
          text-decoration: none;
          color: #fff; }
          .weather-card .top .wrapper .temp a .temp-type {
            font-size: 85px;
            color: #fff; }
        .weather-card .top .wrapper .temp .temp-value {
          display: inline-block;
          font-size: 85px;
          font-weight: 600;
          color: #fff; }
        .weather-card .top .wrapper .temp .deg {
          display: inline-block;
          font-size: 35px;
          font-weight: 600;
          color: #fff;
          vertical-align: top;
          margin-top: 10px; }
        .weather-card .top .wrapper .temp .temp-type {
          color: #fff;
          font-size: 48px; }
    .weather-card .top:after {
      content: "";
      height: 100%;
      width: 100%;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      background: rgba(0, 0, 0, 0.5); }
  .weather-card .bottom {
    padding: 0 30px;
    background: #fff; }
    .weather-card .bottom .wrapper .forecast {
      overflow: hidden;
      margin: 0;
      font-size: 0;
      padding: 0;
      padding-top: 15px;
      max-height: 250px; }
      .weather-card .bottom .wrapper .forecast a {
        text-decoration: none;
        color: #000; }
      .weather-card .bottom .wrapper .forecast .go-up {
        text-align: center;
        display: block;
        font-size: 25px;
        margin-bottom: 10px; }
      .weather-card .bottom .wrapper .forecast li {
        display: block;
        font-size: 25px;
        font-weight: 400;
        color: rgba(0, 0, 0, 0.25);
        line-height: 1em;
        margin-bottom: 30px; }
        .weather-card .bottom .wrapper .forecast li .weather-item {
          padding: 5px; }
        .weather-card .bottom .wrapper .forecast li .date {
          display: inline-block; }
        .weather-card .bottom .wrapper .forecast li .condition {
          display: inline-block;
          vertical-align: middle;
          float: right;
          font-size: 25px; }
          .weather-card .bottom .wrapper .forecast li .condition .temp {
            display: inline-block;
            vertical-align: top;
            font-family: "Montserrat", sans-serif;
            font-size: 20px;
            font-weight: 400;
            padding-top: 2px; }
            .weather-card .bottom .wrapper .forecast li .condition .temp .deg {
              display: inline-block;
              font-size: 10px;
              font-weight: 600;
              margin-left: 3px;
              vertical-align: top; }
            .weather-card .bottom .wrapper .forecast li .condition .temp .temp-type {
              font-size: 20px;
              color: #fff; }
        .weather-card .bottom .wrapper .forecast li.active {
          color: rgba(0, 0, 0, 0.8); }
  .weather-card.rain .top {
    background: url(/react-weather-app/static/media/rain.afdef20d.jpg) no-repeat;
    background-size: cover;
    background-position: center center; }

